@import "../../styles/_colors.scss";

.Footer {
  position: relative;
  max-width: 100%;
  top: 125px;
  height: 96px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 -1px 0 $color__elements;
  padding: 0 25px;
  margin:  auto 0;
  background-color: $color__white;

  &__nav-link {
    position: relative;
    padding: 22px 0;
    font-weight: bold;
    line-height: 11px;
    color: $color__secondary;
    text-decoration: none;
    font-size: 12px;
    letter-spacing: 0.04em;
    transition: 0.3s;
  }

  &__nav-link:hover {
    color:#81a0b4;
    transform: scale(0.2);
  }

  &__list {
    display: flex;
    list-style: none;
    @media screen and (max-width: 600px) {
      flex-direction: column;
      justify-content: space-around;
      height: 100px;
    }
  }

  &__item {
    margin-right: 64px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__left {
    display: flex;
    align-items: center;

    @media screen and (max-width: 600px) {
      display: none;
    }
  }

  &__left::after {
    position: absolute;
    content: url('../../styles/images/footer-logo-ok-sign.svg');
    left: 70px;
    bottom: 47px;
  }

  &__right {
    display: flex;
    align-items: center;
  }

  &__backTo {
    font-weight: 600;
    font-size: 12px;
    line-height: 15px;
    color: $color__secondary;
    margin-right: 16px;
  }

  &__backhome {
    outline: none;
    cursor: pointer;
    width: 32px;
    height: 32px;
    background-image: url("../../styles/images/backto-arrow.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-color: $color__white;
    border: 1px solid $color__icons;
    border-radius: 50%;
    box-sizing: border-box;
  }
}
